*,
body,
h1,
h2,
h3,
div,
section,
p,
ul,
li {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

ul,
li {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

address {
  font-style: normal;
}

/* common */
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap");

@import url("https://fonts.googleapis.com/css2?family=Kaisei+Tokumin:wght@400;500;700;800&display=swap");

@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600&display=swap");

html {
  // font-family: "Inter", sans-serif;
  font-family: "Open Sans", sans-serif;
  font-size: 11px;
  scroll-behavior: smooth;
}

body {
  width: 100vw;
  overflow-x: hidden;
}

h1,
h2,
h3,
h4 {
  font-family: "Kaisei Tokumin", serif;
}

// variables
$width: 80vw;
$max-width: 1200px;

// first-randering transition
#container {
  > * {
    opacity: 0;
    transition: 1.5s 0.5s;
  }

  &.is-loaded > * {
    opacity: 1;
  }
}

// page transition
.casestudy,
.main {
  position: absolute;
  background: white;

  &.page-enter-done {
    z-index: 2;
  }

  &.page-exit {
    z-index: 1;
  }
}

// global effect
.hover-button {
  background: linear-gradient(
      90deg,
      transparent calc(100% / 3),
      currentColor 0 calc(300% / 3),
      transparent 0
    )
    var(--d, 100%) 100% / 200% 1px repeat-x;
  cursor: pointer;

  &:hover {
    --d: -100%;
    transition: 0.7s;
  }
}
